"use client";
import Image from "next/image";
import Link from "next/link";
import { motion } from "framer-motion";
import Counter from "@/components/Counter";

const fadeUp = { hidden: { opacity: 0, y: 40 }, visible: { opacity: 1, y: 0 } };

export default function VolunteeringPage() {
  return (
    <main className="bg-black text-white pt-28 pb-12 px-4 md:px-16">
      {/* Hero */}
      <motion.section
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6 }}
        viewport={{ once: true }}
        className="text-center mb-8"
      >
        <h1 className="text-4xl md:text-5xl font-bold text-pink-500 mb-4">
          Volunteer With Us
        </h1>
        <p className="text-lg md:text-xl text-gray-300 mx-auto max-w-3xl mb-8">
          Join hands with Avasa Foundation and become a part of a meaningful journey that creates real impact.
        </p>
        <div className="flex flex-wrap justify-center gap-4">
          <Link href="#opportunities">
            <button className="bg-pink-600 hover:bg-pink-700 text-white font-semibold py-3 px-6 rounded-full transition duration-300">
              View Opportunities
            </button>
          </Link>
          <Link href="#apply">
            <button className="border-2 border-pink-500 text-pink-500 hover:bg-pink-500 hover:text-white font-semibold py-3 px-6 rounded-full transition duration-300">
              Apply Now
            </button>
          </Link>
        </div>
      </motion.section>

      {/* Counters */}
      <motion.section
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6, delay: 0.2 }}
        viewport={{ once: true }}
        className="grid grid-cols-1 sm:grid-cols-3 gap-8 text-center mb-8"
      >
        <div>
          <Counter to={100} />
          <p className="text-gray-300 mt-2">Volunteers Engaged</p>
        </div>
        <div>
          <Counter to={8} />
          <p className="text-gray-300 mt-2">Projects Completed</p>
        </div>
        <div>
          <Counter to={2000} />
          <p className="text-gray-300 mt-2">Lives Impacted</p>
        </div>
      </motion.section>

      {/* Why Volunteer Section */}
      <motion.section
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6, delay: 0.3 }}
        viewport={{ once: true }}
        className="mb-8"
      >
        <div className="bg-gray-900 rounded-lg p-8">
          <h2 className="text-3xl font-bold text-pink-500 mb-6 text-center">
            Why Volunteer with Avasa Foundation?
          </h2>
          <div className="grid md:grid-cols-2 gap-8">
            <div>
              <h3 className="text-xl font-semibold text-pink-400 mb-3">Make Real Impact</h3>
              <p className="text-gray-300 mb-4">
                Your time and skills directly contribute to improving lives and creating lasting change in communities.
              </p>
              <h3 className="text-xl font-semibold text-pink-400 mb-3">Develop Skills</h3>
              <p className="text-gray-300">
                Gain valuable experience in project management, communication, leadership, and social impact measurement.
              </p>
            </div>
            <div>
              <h3 className="text-xl font-semibold text-pink-400 mb-3">Join a Community</h3>
              <p className="text-gray-300 mb-4">
                Connect with like-minded individuals who share your passion for social change and community development.
              </p>
              <h3 className="text-xl font-semibold text-pink-400 mb-3">Flexible Commitment</h3>
              <p className="text-gray-300">
                Choose opportunities that fit your schedule, from one-time events to ongoing projects.
              </p>
            </div>
          </div>
        </div>
      </motion.section>

      {/* Opportunity Cards */}
      <section id="opportunities" className="mb-8">
        <h2 className="text-3xl font-bold text-pink-500 mb-8 text-center">
          Volunteer Opportunities
        </h2>
        <div className="grid gap-8 md:grid-cols-3">
          {[
            {
              img: "/images/volunteer1.jpg",
              alt: "Volunteer creating digital media posters",
              title: "Design & Media",
              desc: "Create posters, reels, and presentations that tell powerful stories.",
              skills: ["Graphic Design", "Video Editing", "Social Media"],
              timeCommitment: "5-10 hours/week",
              cta: "Join the Creative Team",
            },
            {
              img: "/images/volunteer2.jpg",
              alt: "Volunteers engaging with community",
              title: "Outreach & Events",
              desc: "Plan and participate in campaigns, drives, and fundraising events.",
              skills: ["Event Planning", "Communication", "Community Engagement"],
              timeCommitment: "Flexible",
              cta: "Start Volunteering",
            },
            {
              img: "/images/volunteer3.jpg",
              alt: "Volunteer conducting field research",
              title: "Research & Documentation",
              desc: "Conduct surveys, analyze impact, and compile reports.",
              skills: ["Research", "Data Analysis", "Report Writing"],
              timeCommitment: "8-12 hours/week",
              cta: "Get Involved",
            },
          ].map((item, idx) => (
            <motion.div
              key={item.title}
              initial="hidden"
              whileInView="visible"
              variants={fadeUp}
              transition={{ duration: 0.6, delay: 0.4 + idx * 0.2 }}
              viewport={{ once: true }}
              className="bg-gray-900 rounded-lg shadow-md overflow-hidden h-full"
            >
              <Image
                src={item.img}
                alt={item.alt}
                width={500}
                height={300}
                className="w-full object-cover h-48"
              />
              <div className="p-6">
                <h3 className="text-xl font-semibold text-pink-500 mb-3">
                  {item.title}
                </h3>
                <p className="text-gray-300 mb-4">{item.desc}</p>
                
                <div className="mb-4">
                  <h4 className="text-pink-400 font-semibold mb-2">Skills Needed:</h4>
                  <div className="flex flex-wrap gap-2">
                    {item.skills.map((skill, skillIdx) => (
                      <span key={skillIdx} className="bg-pink-600 text-white px-3 py-1 rounded-full text-sm">
                        {skill}
                      </span>
                    ))}
                  </div>
                </div>
                
                <div className="mb-4">
                  <span className="text-pink-400 font-semibold">Time Commitment: </span>
                  <span className="text-gray-300">{item.timeCommitment}</span>
                </div>

                <Link href="/join-us/volunteering#apply">
                  <span className="text-pink-400 hover:underline font-semibold">{item.cta} â†’</span>
                </Link>
              </div>
            </motion.div>
          ))}
        </div>
      </section>

      {/* Volunteer Stories */}
      <motion.section
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6, delay: 0.8 }}
        viewport={{ once: true }}
        className="mb-8"
      >
        <h2 className="text-3xl font-bold text-pink-500 mb-8 text-center">
          Volunteer Stories
        </h2>
        <div className="grid md:grid-cols-2 gap-8">
          <div className="bg-gray-900 rounded-lg p-6">
            <div className="flex items-center mb-4">
              <div className="w-12 h-12 bg-pink-500 rounded-full flex items-center justify-center mr-4">
                <span className="text-white font-bold">S</span>
              </div>
              <div>
                <h4 className="text-pink-400 font-semibold">Sarah M.</h4>
                <p className="text-gray-400 text-sm">Design Volunteer</p>
              </div>
            </div>
            <p className="text-gray-300 italic">
              "Volunteering with Avasa Foundation has been incredibly rewarding. I've been able to use my design skills to create materials that actually make a difference in people's lives."
            </p>
          </div>
          <div className="bg-gray-900 rounded-lg p-6">
            <div className="flex items-center mb-4">
              <div className="w-12 h-12 bg-pink-500 rounded-full flex items-center justify-center mr-4">
                <span className="text-white font-bold">R</span>
              </div>
              <div>
                <h4 className="text-pink-400 font-semibold">Raj K.</h4>
                <p className="text-gray-400 text-sm">Outreach Volunteer</p>
              </div>
            </div>
            <p className="text-gray-300 italic">
              "The community events I've helped organize have connected me with amazing people and shown me the real impact of grassroots work."
            </p>
          </div>
        </div>
      </motion.section>

      {/* Process Section */}
      <motion.section
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6, delay: 0.9 }}
        viewport={{ once: true }}
        className="mb-8"
      >
        <h2 className="text-3xl font-bold text-pink-500 mb-8 text-center">
          How to Get Started
        </h2>
        <div className="grid md:grid-cols-4 gap-6">
          {[
            { step: "1", title: "Apply", desc: "Fill out our volunteer application form" },
            { step: "2", title: "Interview", desc: "Brief conversation to match you with the right opportunity" },
            { step: "3", title: "Training", desc: "Get oriented with our mission and your role" },
            { step: "4", title: "Start", desc: "Begin making a difference in your community" },
          ].map((item, idx) => (
            <div key={idx} className="text-center">
              <div className="w-16 h-16 bg-pink-600 rounded-full flex items-center justify-center mx-auto mb-4">
                <span className="text-white font-bold text-xl">{item.step}</span>
              </div>
              <h3 className="text-pink-400 font-semibold mb-2">{item.title}</h3>
              <p className="text-gray-300 text-sm">{item.desc}</p>
            </div>
          ))}
        </div>
      </motion.section>

      {/* CTA */}
      <motion.section
        id="apply"
        initial="hidden"
        whileInView="visible"
        variants={fadeUp}
        transition={{ duration: 0.6, delay: 1 }}
        viewport={{ once: true }}
        className="text-center py-12 bg-gray-900 rounded-lg"
      >
        <h2 className="text-3xl font-semibold text-pink-500 mb-4">
          Ready to Make a Difference?
        </h2>
        <p className="text-gray-300 mb-6 max-w-xl mx-auto">
          Fill out the volunteer form and we'll match you with the perfect opportunity to create meaningful impact.
        </p>
        <Link href="/apply/volunteer">
          <button className="bg-pink-600 hover:bg-pink-700 text-white font-bold py-3 px-6 rounded-full transition duration-300">
            Sign Up to Volunteer
          </button>
        </Link>
      </motion.section>
    </main>
  );
} 